import Pagination from 'rc-pagination';

 
const Mypagination = ({limit, setLimit, setPage, count, page, GetSearch}) =>{
    // console.log(limit, count, page,"----a--");
    return (
        <div className="relative flex flex-row w-full justify-between px-2">
            <div></div>
            <Pagination
                pageSize={limit}
                current={page}
                // hideOnSinglePage
                className="" 
                total={count} 
                // showTotal={(total, range) =><div className="hidden md:flex absolute top-0 left-2">{`${range[0]} - ${range[1]} –∏–∑ ${total}`}</div>}
                onChange={(page) =>{
                    setPage(page);   
                    window.scrollTo({
                    top: 0,
                    behavior: "smooth"
                    });
                } 
            }
            />
            {/* <select className="focus:outline-none p-1" value={limit} onChange={(e) => setLimit(e.target.value)}> 
                <option value={10}>
                    10
                </option>
                <option value={20}>
                    20
                </option>
                <option value={30}>
                    30
                </option>
                <option value={40}>
                    40
                </option>
                <option value={50}>
                    50
                </option>
            </select> */}
        </div>
    );
};
 
export default Mypagination;